.main-credential {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: -20px 0 0px;
		margin-top: 20px;
}

h1 {
    font-weight: bold;
    margin: 0;
}
h3{
    font-weight: bold;
    color: #00e472;
}
p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: .5px;
    margin: 20px 0 30px;
}

span {
    font-size: 13px;
}

a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 1px 0;
}
a:hover{
    text-decoration: none;
    color: #00e472;
}
::-webkit-input-placeholder {
    color: #3a3a3a;
    font-weight: 600;
}
:-ms-input-placeholder {
    color: #3a3a3a;
    font-weight: 600;
}
::placeholder {
    color: #3a3a3a;
    font-weight: 600;
}
nav{
    background: #191919;
}
.fixed-top{
    border: none;
}
.navbar .navbar-brand{
    color: #00e472;
    
}
.form-title{
    margin-top: 30px;
}
.form-container form {
    margin: 0 15px;
    background: #191919;
    display: flex;
    flex-direction: column;
    padding:  0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.social-container {
    margin: 20px 0;
}

.SignIn-Form{
    border-radius: 7px;
}

.form-container input {
    background: #eee;
    border: none;
    outline: none;
    border-radius: 8px;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
}

.sign-up-container{
    margin-top: 55rem;
}

.google-signin-btn{
    border: 1px solid #00e472;
}
.google-signin-btn:hover{
    background: #00e472;
    color: #000000;
    border: 1px solid #00e472;
    box-shadow: 0 0 10px #00e472;
}

button {
    border-radius: 8px;
    border: 1px solid #00e472;
    background: rgb(17, 17, 17);
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    padding: 12px 45px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: .10s ease-in;
}

button:active {
    -webkit-transform: scale(.95);
            transform: scale(.95);
}
button:hover{
    background: #00e472;
    color: rgb(0, 0, 0);
    box-shadow: 0px 0px 15px #00e472;
}

button:focus {
    outline: none;
}

button.ghost {
    color: #000000;
    background: transparent;
    border-color: rgb(0, 0, 0);
}
button.ghost:hover {
    background: rgb(22, 22, 22);
    color: #ffffff;
    border: none;
    box-shadow: 0px 0px 15px #000000;
}






.footer {
	margin-top: 25px;
	text-align: center;
}


@media (max-width: 765px) {
    .navbar-brand {
        font-size: 20px;
    }
}
