.posts{
    margin: 2rem 0 0 0;
    padding: 0;
    background: none;
    margin-left: 5px;
    margin-right: 8px;
}

a.file-info{
    font-size: 15px;
    color: rgb(0, 0, 0);
    font-weight: bolder;
}
.file-info:hover{
    color: #000000;
    text-decoration: none;
}
.false{
    display: flex;
    align-self: flex-start;
}
.true{
    display: flex;
    align-self: flex-end;
    flex-direction: row-reverse;
}
.posts{
    display: inline;
    display: flex;
    flex-direction: column-reverse;
    
}

.nav-tab{
    border: none;
    outline: none;
    background-color: #0d0d0d;
    -webkit-text-decoration-style: none;
            text-decoration-style: none;
}
.nav-link{
    font-size: 14px;
    border-radius: 6px;
}
.tru .date{
    font-size: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.fals .date{
    font-size: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.user-info{
    font-size: 11px;
    color: #fff;
}
.if-true.user-info{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.if-false.user-info{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.avatar-false{
    margin-top: 20px;
    max-height: 55px;
    max-width: 55px;
    border: 2px solid #00e472;
}
.avatar-true{
    margin-top: 20px;
    max-height: 55px;
    max-width: 55px;
    border: 2px solid #00e472;
}

.if-tru{
    background-color: rgb(240, 238, 238);
    border-top-right-radius: 0px;
    border-top-left-radius: 22px;
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 22px;
}
.if-fals{
    background-color: rgb(240, 238, 238);
    border-top-left-radius: 0px;
    border-top-right-radius: 22px;
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 22px;
}


  /* MESSAGE-TYPING-FIELD */
#message-typing-box{
    padding: 0 10px;
    background: #0c0c0c!important;
    /*border-top: 2px solid rgb(66, 66, 66);*/
}
.input-group{
    padding: 0;
}
input.form-control{
    width: 100%;
    background-color: rgb(10, 10, 10);
    border: 1px solid #00e472;
    font-family: 'Cascadia Code', sans-serif;
}
input.form-control:focus{
    border: none;
    outline: none;
    box-shadow: none;
}
.file-btn-label {
    background-color: #00e472;
    color: white;
    padding: 6px;
    cursor: pointer;
    margin-top: 1rem;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    /*border-top: 2px solid #00e472;*/
}
[type="file"] {
    height: 0;
    overflow: hidden;
    width: 0;
}
.button-send {
    background-color: #0c0c0c!important;
    border: none;
    outline: none;
    color: white;
    padding: 0 7px;
    margin-top: 7px;
    font-size: 19px;
    cursor: pointer;
}
.button-send:focus{
    border: none;
    outline: none;
}
.button-send:hover{
    background-color: #0c0c0c!important;
    color: white;
    box-shadow: none;
}

@media (max-width: 1000px) {
    
    .if-fals a.file-info{
        padding: 0 8px 0 0;
        font-size: 10px;
        color: rgb(0, 0, 0);
        font-weight: bolder;
    }
    .if-tru a.file-info{
        padding: 0 8px 0 0;
        font-size: 10px;
        color: rgb(0, 0, 0);
        font-weight: bolder;
    }
    .file-info:hover{
        color: #000000;
        text-decoration: none;
    }
    .false{
        margin-right: 20px;
    }
    .nav-tab{
        border: none;
        color: rgb(0, 0, 0);
        -webkit-text-decoration-style: none;
                text-decoration-style: none;
    }
    .nav-link{
        font-size: small;
        max-width: 200px;
    }
    .date{
        font-size: 9px;
    }
    .tru .date{
        margin: 10px 0 0 0;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
    }
    .fals .date{
        margin: 10px 0 0 0;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
    }
    .user-info{
        font-size: 10px;
        color: #fff;
    }
    [type="btn"]{
        max-width: 10%; 
    }
    .avatar{
        max-height: 40px;
        max-width: 40px;
        border: 3px solid #00e472;
    }
    .nav-link{
        display: flex;
    flex-wrap: wrap;
    }
    
    
}
